.promo {
    padding-top: 73px;
}

.promo__title {
    margin-bottom: 87px;
    font-size: 57px;
    font-weight: 700;
    line-height: 1;
    letter-spacing: 0.04rem;
    background-image: linear-gradient(90deg, white, #ff3f04);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}


.promo__subtitle {
    display: block;
    font-size: 26px;
    font-weight: 300;
    letter-spacing: -0.03rem;
}

.promo__button {
    position: relative;
    padding: 8px 22px;
    color: #818287;
    font-size: 14px;
    font-weight: 100;
    border: 1px solid #3969a6;
    border-image: linear-gradient(to right top, #3969a6, #0f1f2d, #3969a6);
    border-image-slice: 1;
    z-index: 5;
}

.promo__button::before,
.promo__button::after {
    position: absolute;
    content: '';
    width: 5px;
    height: 5px;
}

.promo__button::before {
    left: -1px;
    bottom: -1px;
    border-left: 1px solid white;
    border-bottom: 1px solid white;
}

.promo__button::after {
    right: -1px;
    top: -1px;
    border-right: 1px solid white;
    border-top: 1px solid white;
}


.promo__button-container {
    width: fit-content;
    position: relative;
}

.promo__button-container::before,
.promo__button-container::after {
    position: absolute;
    content: '';
    width: 7px;
    height: 7px;
}

.promo__button-container::before {
    left: -1px;
    bottom: -1px;
    border-left: 3px solid white;
    border-bottom: 3px solid white;
    filter: blur(2px);
}

.promo__button-container::after {
    right: -1px;
    top: -1px;
    border-right: 3px solid white;
    border-top: 3px solid white;
    filter: blur(2px);
}



@media (max-width:1440px) {
    .promo {
        padding-top: 165px;
    }

    .promo__title {
        margin-bottom: 180px;
        font-size: 52px;
    }

    .promo__subtitle {
        font-size: 24px;
    }
}

@media (max-width:1024px) {
    .promo {
        margin-bottom: 100px;
        padding-top: 0;
    }

    .promo__title {
        margin-bottom: 100px;
        width: fit-content;
    }

    .promo__button {
        font-size: 18px;
        z-index: 0;
    }

}

@media (max-width:440px) {
    .promo {
        margin-bottom: 30px;
    }

    .promo__title {
        font-size: 40px;
    }

    .promo__subtitle {
        font-size: 22px;
    }
}